<template>
  <span class="tb-switch" :style="bgStyle" tabindex="0" @click="!disabled ? $emit('input', !value) : null" @keyup.enter.prevent="!disabled ? $emit('input', !value) : null" @keyup.space.prevent="!disabled ? $emit('input', !value) : null">
    <input type="checkbox" :name="name" style="display:none;" v-model="value" />
    <span :style="dotStyle" class="dot">
      <span v-show="checkedText && value" :style="textStyle" class="text">
        {{ checkedText }}
      </span>

      <span v-show="uncheckedText && !value" :style="textStyle" class="text">
        {{ uncheckedText }}
      </span>
    </span>
  </span>
</template>

<script>
export default {
  name: "tbSwitch",
  props: {
    // 
    value: {
      type: Boolean,
      default: false,
    },
    // name
    name: {
      type: String,
      default: "switch",
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    //   
    reverse: {
      type: Boolean,
      default: false,
    },
    // 
    width: {
      type: [Number, String],
      default: 75,
    },
    // 
    height: {
      type: [Number, String],
      default: 25,
    },
    // 
    checkedText: {
      type: String,
      default: null,
    },
    // 
    uncheckedText: {
      type: String,
      default: null,
    },
    // 
    checkedBg: {
      type: String,
      default: "#1089ff",
    },
    // 
    uncheckedBg: {
      type: String,
      default: "#939393",
    },
    // 
    checkedColor: {
      type: String,
      default: "#fff",
    },
    // 
    uncheckedColor: {
      type: String,
      default: "#fff",
    },
    // 
    dotColor: {
      type: String,
      default: "#fff",
    },
    // 
    fontSize: {
      type: [Number, String],
      default: "12",
    },
    // 
    fontWeight: {
      type: [Number, String],
      default: "normal",
    },
  },
  watch: {
    // value  change
    value: function(newVal, oldVal) {
      this.$emit("change", newVal);
    },
  },
  computed: {
    // 
    bgStyle() {
      const styles = {
        // 
        width: `${this.width}px`,
        height: `${this.height}px`,
        //  
        background: this.value && !this.disabled ? this.checkedBg : this.uncheckedBg,
      };
      return styles;
    },
    // 
    dotStyle() {
      const styles = {
        // 
        background: this.dotColor,
        //   
        width: `${this.height - 8}px`,
        height: `${this.height - 8}px`,
        "min-width": `${this.height - 8}px`,
        "min-height": `${this.height - 8}px`,
        // 
        "margin-left": this.value ? `${this.width - (this.height - 3)}px` : "5px",
      };
      //   
      if ((!this.value && this.reverse) || (this.value && !this.reverse)) {
        styles.marginLeft = `${this.width - (this.height - 3)}px`;
      } else if ((this.value && this.reverse) || (!this.value && !this.reverse)) {
        styles.marginLeft = "5px";
      }
      return styles;
    },
    // 
    textStyle() {
      //   
      const styles = {
        "font-weight": this.fontWeight,
        "font-size": `${this.fontSize}px`,
        color: this.value && !this.disabled ? this.checkedColor : this.uncheckedColor,
        // 
        right: this.value ? `${this.height - 3}px` : "auto",
        left: this.value ? "auto" : `${this.height - 3}px`,
      };
      // 
      if (!this.value && this.reverse) {
        styles.right = `${this.height - 3}px`;
        styles.left = "auto";
      } else if (this.value && this.reverse) {
        styles.left = `${this.height - 3}px`;
        styles.right = "auto";
      }
      return styles;
    },
  },
};
</script>

<style>
.tb-switch {
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 9999px;
  overflow: hidden;
  transition: background-color ease 0.2s, width ease 0.2s, height ease 0.2s;
}
.tb-switch .dot {
  position: relative;
  display: flex;
  align-items: center;
  border-radius: 9999px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: margin ease 0.2s;
}
.tb-switch .text {
  position: absolute;
  font-family: inherit;
  user-select: none;
  white-space: nowrap;
}
@media all and (-ms-high-contrast: none) {
  .tb-switch .text {
    top: 50%;
    transform: translateY(-50%);
  }
}
@media (prefers-reduced-motion) {
  .tb-switch,
  .tb-switch *,
  .tb-switch *::before,
  .tb-switch *::after {
    animation: none !important;
    transition: none !important;
    transition-duration: none !important;
  }
}
</style>
