import Vue from "vue";
import Message from "./main.vue";

Message.newInstance = (properties) => {
  const _props = properties || {};
  //   main.vue 
  const Instance = new Vue({
    name: "parentVue",
    render(h) {
      return h(Message, {
        props: _props,
      });
    },
  });
  // 
  const component = Instance.$mount();
  // dom
  document.body.appendChild(component.$el);
  // messageVnode  new Vue(),Messagenew Vue
  const message = Instance.$children[0];

  // vnode
  return {
    notice(noticeProps) {
      // message add name
      message.add(noticeProps);
    },
    remove(name) {
      // message close
      message.close(name);
    },
    component: message,
    destroy(element) {
      // message closeAll
      message.closeAll();
      setTimeout(function() {
        document.body.removeChild(document.getElementsByClassName(element)[0]);
      }, 500);
    },
  };
};
const prefixKey = "tb_message_key_";
let messageInstance;
let name = 1;

// 
function getMessageInstance() {
  messageInstance =
    messageInstance ||
    Message.newInstance({
      styles: {
        top: "24px",
      },
    });
  return messageInstance;
}

// 
function notice(options) {
  let instance = getMessageInstance();
  // name  key
  let _notice = Object.assign({ name: `${prefixKey}${name}` }, options);
  // 
  instance.notice(_notice);
  // 
  return (function() {
    let target = name++;
    return function() {
      // js name  this.$message({ type: "success", duration: 0, content: "look" })()  
      instance.remove(`${prefixKey}${target}`);
    };
  })();
}

let defaultOption = {
  transitionName: "move-up",
  type: "primary",
  content: "",
  duration: 3,
  closable: false,
  render: function() {},
  onClose: function() {},
};
// 
export default {
  name: "Message",
  // messageoptions
  message(options) {
    // 
    let _option = {};
    if (typeof options === "string") {
      _option = Object.assign({}, defaultOption, { content: options });
      return notice(_option);
    }
    // 
    _option = Object.assign({}, defaultOption, options);
    return notice(_option);
  },
  destroy() {
    let instance = getMessageInstance();
    messageInstance = null;
    instance.destroy("tb-message");
  },
};
