<template>
  <div :class="[prefixCls]">
    <!--      drop <div>      paste @paste="handlePaste"       dragover     dragleave -->
    <div :class="classes" @click="handleClick" @drop.prevent="onDrop" @dragover.prevent="dragOver = true" @dragleave.prevent="dragOver = false">
      <input ref="input" type="file" :class="[prefixCls + '-input']" @change="handleChange" :multiple="multiple" :accept="accept" />
      <slot>
        <tb-button icon="icon-upload"> </tb-button>
      </slot>
    </div>
    <!--slot="tip"-->
    <slot name="tip"></slot>
    <!--  showFileListfileList handleRemovehandlePreview-->
    <tb-upload-list v-if="showFileList" :files="fileList" @file-remove="handleRemove" @file-preview="handlePreview"></tb-upload-list>
  </div>
</template>

<script>
import { oneOf } from "../utils/utils";
import ajax from "./ajax";
import Emitter from "../mixins/emitter";
import tbUploadList from "./tbUploadList.vue";

const prefixCls = "tb-upload";
export default {
  name: "tbUpload",
  mixins: [Emitter],
  components: { tbUploadList },
  props: {
    // 
    action: {
      type: String,
      required: true,
    },
    // 
    headers: {
      type: Object,
      default() {
        return {};
      },
    },
    // input filemultiple
    multiple: {
      type: Boolean,
      default: false,
    },
    // 
    data: {
      type: Object,
    },
    // 
    name: {
      type: String,
      default: "file",
    },
    // cookie  cookie   xhr.withCredentials = true response Access-Control-Allow-Origin*
    withCredentials: {
      type: Boolean,
      default: false,
    },
    // fileList
    showFileList: {
      type: Boolean,
      default: true,
    },
    // select  drag  select
    type: {
      type: String,
      validator(value) {
        return oneOf(value, ["select", "drag"]);
      },
      default: "select",
    },
    //   accept format 
    format: {
      type: Array,
      default() {
        return [];
      },
    },
    // ,input  accept   
    accept: {
      type: String,
    },
    //  KB
    maxSize: {
      type: Number,
    },
    // , false  Promise 
    beforeUpload: Function,
    //  event, file, fileList
    onProgress: {
      type: Function,
      default() {
        return {};
      },
    },
    //  response, file, fileList
    onSuccess: {
      type: Function,
      default() {
        return {};
      },
    },
    //  error, file, fileList
    onError: {
      type: Function,
      default() {
        return {};
      },
    },
    //  file, fileList
    onRemove: {
      type: Function,
      default() {
        return {};
      },
    },
    //  file  file.response 
    onPreview: {
      type: Function,
      default() {
        return {};
      },
    },
    //  file, fileList
    onExceededSize: {
      type: Function,
      default() {
        return {};
      },
    },
    //  file, fileList
    onFormatError: {
      type: Function,
      default() {
        return {};
      },
    },
    // [{name: 'img1.jpg',url: 'http://www.xxx.com/img1.jpg' }, { name: 'img2.jpg', url: 'http://www.xxx.com/img2.jpg' }]
    defaultFileList: {
      type: Array,
      default() {
        return [];
      },
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      prefixCls: prefixCls,
      dragOver: false,
      fileList: [],
      tempIndex: 1,
    };
  },
  computed: {
    //  type 
    classes() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-select`]: this.type === "select",
          [`${prefixCls}-drag`]: this.type === "drag",
          //    
          [`${prefixCls}-dragOver`]: this.type === "drag" && this.dragOver,
        },
      ];
    },
  },
  methods: {
    // 
    handleClick() {
      if (this.disabled) return;
      this.$refs.input.click();
    },
    //   
    handleChange(e) {
      const files = e.target.files;
      if (!files) {
        return;
      }
      // 
      this.uploadFiles(files);
      // file  
      this.$refs.input.value = null;
    },
    //    this.dragOverfalse  
    onDrop(e) {
      this.dragOver = false;
      // 
      if (this.disabled) return;
      //    DataTransfer.files   
      this.uploadFiles(e.dataTransfer.files);
    },
    
    // 
    uploadFiles(files) {
      //   Array.prototype.slice.call  
      let postFiles = Array.prototype.slice.call(files);
      // 
      if (!this.multiple) postFiles = postFiles.slice(0, 1);

      // return
      if (postFiles.length === 0) return;

      // 
      postFiles.forEach((file) => {
        this.upload(file);
      });
    },
    // 
    upload(file) {
      //  beforeUpload  this.post 
      if (!this.beforeUpload) {
        return this.post(file);
      }
      //  beforeUpload 
      const before = this.beforeUpload(file);
      // returnpromise  .then  ajax
      if (before && before.then) {
        // .then resolvefileif
        before.then(
          (processedFile) => {
            if (Object.prototype.toString.call(processedFile) === "[object File]") {
              this.post(processedFile);
            } else {
              this.post(file);
            }
          },
          () => {
            // 
            // this.$emit('cancel', file);
          }
        );
      } else if (before !== false) {
        // before false 
        this.post(file);
      } else {
        // 
        // this.$emit('cancel', file);
      }
    },
    // 
    post(file) {
      //   
      if (this.format.length) {
        // 
        const fileFormat = file.name
          .split(".")
          .pop()
          .toLocaleLowerCase();
        //   format
        const checked = this.format.some((item) => item.toLocaleLowerCase() === fileFormat);
        // ,
        if (!checked) {
          this.onFormatError(file, this.fileList);
          return false;
        }
      }

      // 
      if (this.maxSize) {
        // maxSize    file, fileList
        if (file.size > this.maxSize * 1024) {
          this.onExceededSize(file, this.fileList);
          return false;
        }
      }
      //   fileList    tb-upload-list 
      this.handleStart(file);
      //     file
      // let formData = new FormData();
      // formData.append(this.name, file);

      /**
       * @description: ajax  
       * @param {*}
       * @return {*}
       */      
      ajax({
        headers: this.headers, // 
        withCredentials: this.withCredentials, // cookie
        file: file, // 
        data: this.data, //
        filename: this.name, // key
        action: this.action, // 
        // 
        onProgress: (e) => {
          this.handleProgress(e, file);
        },
        // 
        onSuccess: (res) => {
          this.handleSuccess(res, file);
        },
        // 
        onError: (err, response) => {
          this.handleError(err, response, file);
        },
      });
    },
    // fileList
    handleStart(file) {
      file.uid = Date.now() + this.tempIndex++;
      const _file = {
        status: "uploading",// 
        name: file.name,
        size: file.size,
        percentage: 0,
        uid: file.uid,
        showProgress: true,
      };

      this.fileList.push(_file);
    },
    getFile(file) {
      const fileList = this.fileList;
      let target;
      // truefileList
      fileList.every((item) => {
        target = file.uid === item.uid ? item : null;
        return !target;
      });
      return target;
    },
    /**
     * @description: 
     * @param {*} e
     * @param {*} file
     * @return {*}
     */    
    handleProgress(e, file) {
      const _file = this.getFile(file);
      // fileListfilethis.onProgress
      this.onProgress(e, _file, this.fileList);
      //     0 
      _file.percentage = e.percent || 0;
    },
    handleSuccess(res, file) {
      // fileListfile
      const _file = this.getFile(file);

      // 
      if (_file) {
        // finished
        _file.status = "finished";
        // 
        _file.response = res;
        this.onSuccess(res, _file, this.fileList);
        // form-change  
        this.dispatch("tbFormItem", "form-change", _file);

        setTimeout(() => {
          // 500ms
          _file.showProgress = false;
        }, 500);
      }
    },
    handleError(err, response, file) {
      // file
      const _file = this.getFile(file);
      const fileList = this.fileList;
      // 
      _file.status = "fail";
      // 
      fileList.splice(fileList.indexOf(_file), 1);
      // 
      this.onError(err, response, file);
    },

    // fileList x
    handleRemove(file) {
      const fileList = this.fileList;
      fileList.splice(fileList.indexOf(file), 1);
      // 
      this.onRemove(file, fileList);
    },
    //  fileList 
    handlePreview(file) {
      // 
      if (file.status === "finished") {
        // fileList 
        this.onPreview(file);
      }
    },
    // fileList     this.$refs['upload(ref)'].clearFiles()  file
    clearFiles() {
      this.fileList = [];
    },
  },
  watch: {
    // 
    defaultFileList: {
      immediate: true,
      handler(fileList) {
        //   map    percentage100   uid
        this.fileList = fileList.map((item) => {
          item.status = "finished";
          item.percentage = 100;
          item.uid = Date.now() + this.tempIndex++;
          return item;
        });
      },
    },
  },
};
</script>
<style lang="less">
.tb-upload {
  input[type="file"] {
    display: none;
  }
  &-select {
    display: inline-block;
  }
  &-list {
    margin-top: 8px;

    &-file {
      padding: 4px;
      color: #515a6e;
      border-radius: 4px;
      line-height: 1.5em;
      font-size: 12px;
      transition: background-color 0.2s ease-in-out;
      overflow: hidden;
      position: relative;

      & > span {
        cursor: pointer;
        transition: color 0.2s ease-in-out;
        i {
          display: inline-block;
          vertical-align: baseline;
          width: 12px;
          height: 12px;
          color: #515a6e;
          text-align: center;
        }
      }

      &:hover {
        background: #f3f3f3;
        & > span {
          color: #1089ff;
          i {
            color: #515a6e;
          }
        }
        .tb-upload-list-remove {
          opacity: 1;
        }
      }
    }
    &-remove {
      opacity: 0;
      font-size: 18px;
      cursor: pointer;
      float: right;
      margin-right: 4px;
      color: #999;
      transition: all 0.2s ease;
      &:hover {
        color: #444;
      }
    }
  }
}
</style>
