<template>
  <div class="tb-backtop tb-div-wrapper">
    <transition name="fade">
      <tb-button circle icon="icon-arrow-up-filling" class="tb-scroll-button" v-if="tbIsVisible && !text" @click="tbGoToTop(duration)"></tb-button>
      <tb-button class="tb-scroll-button" v-if="tbIsVisible && text" @click="tbGoToTop(duration)">{{ this.text }}</tb-button>
    </transition>
  </div>
</template>

<script>
export default {
  name: "tbBacktop",
  props: {
    // slot
    text: {
      type: String,
      default: "",
    },
    //   goTop  460
    visibleY: {
      type: Number,
      default: 460,
    },
    // 600
    duration: {
      type: Number,
      default: 600,
    },
    
  },
  data() {
    return {
      tbIsVisible: false,
    };
  },
  created() {
    //   addEventListeneronScroll
    window.addEventListener("scroll", this.tbScrollEvent);
  },
  destroyed() {
    // 
    window.removeEventListener("scroll", this.tbScrollEvent);
  },
  methods: {
    tbScrollEvent() {
      // 
      this.tbIsVisible = this.visibleY < window.scrollY;
    },
    //  
    tbGoToTop(duration) {
      if (document.scrollingElement.scrollTop === 0) return;
      const totalScrollDistance = document.scrollingElement.scrollTop;
      let scrollY = totalScrollDistance;
      let oldTimestamp = null;
      function step(newTimestamp) {
        // requestAnimationFrame       /  
        if (oldTimestamp !== null) {
          // 
          scrollY -= (totalScrollDistance * (newTimestamp - oldTimestamp)) / duration;
          if (scrollY <= 0) return (document.scrollingElement.scrollTop = 0);
          document.scrollingElement.scrollTop = scrollY;
        }
        oldTimestamp = newTimestamp;
        // zhen  
        window.requestAnimationFrame(step);
      }
      window.requestAnimationFrame(step);
      // 
      this.$emit("scrolledOnTop");
    },
  },
};
</script>

<style>
.tb-scroll-button {
  cursor: pointer;
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 2501;
  margin-top: 30px;
  border-radius: 20px;
  border: none;
  padding: 8px;
  background-color: black;
  color: #1089ff;
  outline: none;
}
.tb-scroll-button:hover {
  box-shadow: 0 12px 13px -6px black;
  transition: 0.5s;
}
.tb-div-wrapper {
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
