<template>
  <div :class="`tb-checkbox-group ${tbDisabled}`"><slot></slot></div>
</template>

<script>
import Emitter from "../mixins/emitter";
export default {
  name: "tbCheckboxGroup",
  mixins: [Emitter],
  props: {
    value: {
      type: Array,
      default: function() {
        return [];
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "",
    },
    name: {
      type: String,
      default: "",
    },
    // 
    min: {
      type: Number,
      default: 0,
    },
    // 9999
    max: {
      type: Number,
      default: 9999,
    },
  },
  data() {
    return {
      tbDisabled: "",
    };
  },
  watch: {
    disabled(val, oldVal) {
      this.PDisabled();
    },
  },
  provide() {
    return {
      checkboxGroup: this,
    };
  },
  created() {
    this.PDisabled();
  },

  methods: {
    //   emit
    tbCheckboxGroupParentMethod(dom) {
      // ,
      // 
      let val = [...this.value];
      if (dom.checked) {
        val = [...this.value, dom.value];
      } else {
        val.forEach((item, index, arr) => {
          if (item === dom.value) {
            arr.splice(index, 1);
          }
        });
      }
      this.$emit("input", val);
      //   // Change  
      this.$emit("change", val);
      this.dispatch("tbFormItem", "form-change", val);
    },
    // 
    PDisabled() {
      let vm = this;
      if (this.disabled) {
        vm.tbDisabled = " is_disabled ";
      } else {
        vm.tbDisabled = " ";
      }
    },
  },
};
</script>

<style>
.is_disabled {
  pointer-events: none;
  /* cursor: not-allowed !important; */
  opacity: 0.6;
}
</style>
