<template>
  <div :class="`tb-radio-group ${tbDisabled}`"><slot></slot></div>
</template>

<script>
import Emitter from "../mixins/emitter";
export default {
  name: "tbRadioGroup",
  mixins: [Emitter],
  props: {
    value: {
      type: String | Number | Boolean,
      default: "",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "",
    },
    name: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      tbDisabled: "",
    };
  },
  watch: {
    disabled(val, oldVal) {
      this.PDisabled();
    },
  },
  provide() {
    return {
      radioGroup: this,
    };
  },
  created() {
    this.PDisabled();
  },

  methods: {
    //   emit
    tbRadioGroupParentMethod(value) {
      this.$emit("input", value);
      // Change
      this.$emit("change", value);
      console.log(value)
      this.dispatch("tbFormItem", "form-change", value);
    },
    // 
    PDisabled() {
      let vm = this;
      if (this.disabled) {
        vm.tbDisabled = " is_disabled ";
      } else {
        vm.tbDisabled = " ";
      }
    },
  },
};
</script>

<style>
.is_disabled {
  pointer-events: none;
  /* cursor: not-allowed !important; */
  opacity: 0.6;
}
</style>
