<template>
  <div :class="wrapClasses">
    <template v-if="type !== 'textarea'">
      <span :class="[prefixCls + '-group-prepend']" v-if="prepend" v-show="slotReady">
        <slot name="prepend"></slot>
      </span>
      <!---->
      <span :class="closeClasses" v-if="clearable && currentValue && !disabled">
        <i class="iconfont icon-error" @click="handleClear"></i>
      </span>
      <!--  -->
      <i class="iconfont" :class="[icon, prefixCls + '-icon', prefixCls + '-icon-normal']" v-else-if="icon" @click="handleIconClick"></i>
      <!--  -->
      <i class="iconfont icon-search" :class="[prefixCls + '-icon', prefixCls + '-icon-normal', prefixCls + '-search-icon']" v-else-if="search" @click="handleSearch"></i>
      <!--  -->
      <span class="tb-input-suffix" v-else-if="showSuffix">
        <slot name="suffix"><i class="iconfont" :class="[suffix]" v-if="suffix"></i></slot
      ></span>
      <input
        :id="elementId"
        :autocomplete="autocomplete"
        ref="input"
        :type="type"
        :class="inputClasses"
        :placeholder="placeholder"
        :disabled="disabled"
        :maxlength="maxlength"
        :readonly="readonly"
        :name="name"
        :value="currentValue"
        :number="number"
        :autofocus="autofocus"
        @keyup.enter="handleEnter"
        @keyup="handleKeyup"
        @keypress="handleKeypress"
        @keydown="handleKeydown"
        @focus="handleFocus"
        @blur="handleBlur"
        @compositionstart="handleComposition"
        @compositionupdate="handleComposition"
        @compositionend="handleComposition"
        @input="handleInput"
        @change="handleChange"
      />
      <!--   -->
      <span class="tb-input-prefix" v-if="showPrefix">
        <slot name="prefix"><i class="iconfont" :class="[prefix]" v-if="prefix"></i></slot>
      </span>
      <!--  -->
      <span class="tb-input-word-count" v-if="showWordCount">{{ wordCount }}</span>
      <span class="tb-input-group-append" v-if="$slots.append">
        <slot name="append"></slot>
      </span>
    </template>
    <!-- textarea  -->
    <template v-else>
      <textarea
        :id="elementId"
        :wrap="wrap"
        :autocomplete="autocomplete"
        :spellcheck="spellcheck"
        ref="textarea"
        :class="textareaClasses"
        :style="textareaStyle"
        :placeholder="placeholder"
        :disabled="disabled"
        :rows="rows"
        :maxlength="maxlength"
        :readonly="readonly"
        :name="name"
        :value="currentValue"
        :autofocus="autofocus"
        @keyup.enter="handleEnter"
        @keyup="handleKeyup"
        @keypress="handleKeypress"
        @keydown="handleKeydown"
        @focus="handleFocus"
        @blur="handleBlur"
        @compositionstart="handleComposition"
        @compositionupdate="handleComposition"
        @compositionend="handleComposition"
        @input="handleInput"
      ></textarea>
      <!--  -->
      <span class="tb-input-word-count" v-if="showWordCount">{{ wordCount }}</span>
    </template>
  </div>
</template>

<script>
import calcTextareaHeight from "../utils/calcTextareaHeight";
import { oneOf, findComponentUpward } from "../utils/utils";
import Emitter from "../mixins/emitter";

const prefixCls = "tb-input";
export default {
  mixins: [Emitter],
  name: "tbInput",
  props: {
    //     input   text
    type: {
      validator(value) {
        return oneOf(value, ["text", "textarea", "password", "url", "email", "date", "number", "tel"]);
      },
      default: "text",
    },
    // v-model
    value: {
      type: [String, Number],
      default: "",
    },
    // size
    size: {
      validator(value) {
        return oneOf(value, ["medium", "small", "mini"]);
      },
      default: "small",
    },
    // placeholder
    placeholder: {
      type: String,
      default: "",
    },
    // 
    maxlength: {
      type: Number,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    icon: String,
    //  textarea     { minRows: 2, maxRows: 6 }
    autosize: {
      type: [Boolean, Object],
      default: false,
    },
    //  textarea 
    rows: {
      type: Number,
      default: 2,
    },
    // 
    readonly: {
      type: Boolean,
      default: false,
    },
    // name
    name: {
      type: String,
    },
    //  Number 
    number: {
      type: Boolean,
      default: false,
    },
    // 
    autofocus: {
      type: Boolean,
      default: false,
    },
    //    
    spellcheck: {
      type: Boolean,
      default: false,
    },
    //  off  on  
    autocomplete: {
      validator(value) {
        return oneOf(value, ["on", "off"]);
      },
      default: "off",
    },
    // 
    clearable: {
      type: Boolean,
      default: false,
    },
    //  id Form 
    elementId: {
      type: String,
    },
    //  wrap  hard  soft textarea   wrap="soft"  wrap="hard"\n
    wrap: {
      validator(value) {
        return oneOf(value, ["hard", "soft"]);
      },
      default: "soft",
    },
    // 
    prefix: {
      type: String,
      default: "",
    },
    // 
    suffix: {
      type: String,
      default: "",
    },
    // 
    search: {
      type: Boolean,
      default: false,
    },
    // resize  textarea 
    noResize: {
      type: Boolean,
      default: false,
    },
    // 
    showWordCount: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentValue: this.value,
      prefixCls: prefixCls,
      prepend: true,
      append: true,
      slotReady: false,
      textareaStyles: {},
      showPrefix: false,
      showSuffix: false,
      isOnComposition: false,
    };
  },
  watch: {
    // value
    value(val) {
      this.setCurrentValue(val);
    },
    // 
    prefix(val) {
      this.showPrefix = val !== "" || this.$slots.prefix !== undefined;
    },
    // 
    suffix(val) {
      this.showSuffix = val !== "" || this.$slots.suffix !== undefined;
    },
  },
  methods: {
    //    searchsearch 
    handleEnter(event) {
      this.$emit("enter", event);
      if (this.search) this.$emit("search", this.currentValue);
    },
    // 
    handleKeydown(event) {
      this.$emit("keydown", event);
    },
    // 
    handleKeypress(event) {
      this.$emit("keypress", event);
    },
    // 
    handleKeyup(event) {
      this.$emit("keyup", event);
    },
    //  icon 
    handleIconClick(event) {
      this.$emit("click", event);
    },
    // 
    handleFocus(event) {
      this.$emit("focus", event);
    },
    // 
    handleBlur(event) {
      this.$emit("blur", event);
      // 
        if (!findComponentUpward(this, ["DatePicker", "TimePicker", "Cascader", "Search"])) {
          this.dispatch("tbFormItem", "form-blur", this.currentValue);
        }
    },
    //   input  input 
    //  compositionstart  => input => compositionend,    compositionstart  => compositionend => input
    handleComposition(event) {
      if (event.type === "compositionstart") {
        this.isOnComposition = true;
      }
      if (event.type === "compositionend") {
        this.isOnComposition = false;
        this.handleInput(event);
      }
    },
    // input
    handleInput(event) {
      //   input
      if (this.isOnComposition) return;
      let value = event.target.value;
      if (this.number && value !== "") value = Number.isNaN(Number(value)) ? value : Number(value);
      this.$emit("input", value);
      // 
      this.setCurrentValue(value);
      this.$emit("change", event);
    },
    // change
    handleChange(event) {
      this.$emit("input-change", event);
    },
    // 
    setCurrentValue(value) {
      if (value === this.currentValue) return;
      this.$nextTick(() => {
        this.resizeTextarea();
      });
      this.currentValue = value;
      // 
        if (!findComponentUpward(this, ["DatePicker", "TimePicker", "Cascader", "Search"])) {
          this.dispatch("tbFormItem", "form-change", value);
        }
    },
    // autosize
    resizeTextarea() {
      const autosize = this.autosize;
      if (!autosize || this.type !== "textarea") {
        return false;
      }
      const minRows = autosize.minRows;
      const maxRows = autosize.maxRows;

      this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
    },
    // ref
    focus() {
      if (this.type === "textarea") {
        this.$refs.textarea.focus();
      } else {
        this.$refs.input.focus();
      }
    },
    // ref
    blur() {
      if (this.type === "textarea") {
        this.$refs.textarea.blur();
      } else {
        this.$refs.input.blur();
      }
    },
    // icon  
    handleClear() {
      const e = { target: { value: "" } };
      this.$emit("input", "");
      this.setCurrentValue("");
      this.$emit("change", e);
      // clear
      this.$emit("clear");
    },
    // 
    handleSearch() {
      if (this.disabled) return false;
      this.$refs.input.focus();
      this.$emit("search", this.currentValue);
    },
  },
  computed: {
    wrapClasses() {
      return [
        `${prefixCls}-wrapper`,
        {
          [`${prefixCls}-wrapper-${this.size}`]: !!this.size,
          [`${prefixCls}-type-${this.type}`]: this.type,
          // 
          [`${prefixCls}-group-with-prepend`]: this.$slots.prepend,
          // 
          [`${prefixCls}-group-with-append`]: this.$slots.append || this.search,
          [`${prefixCls}-hide-icon`]: this.append, // #554
          [`${prefixCls}-with-search`]: this.search,
          [`${prefixCls}-with-word-count`]: this.showWordCount,
        },
      ];
    },
    inputClasses() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-${this.size}`]: !!this.size,
          [`${prefixCls}-disabled`]: this.disabled,
          [`${prefixCls}-with-prefix`]: this.showPrefix,
          [`${prefixCls}-with-suffix`]: this.showSuffix || this.search,
        },
      ];
    },
    closeClasses() {
      return [prefixCls + "-icon", prefixCls + "-icon-clear", prefixCls + "-icon-normal"];
    },
    textareaStyle() {
      return {
        resize: this.noResize ? "none" : null,
        ...this.textareaStyles,
      };
    },
    textareaClasses() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-disabled`]: this.disabled,
        },
      ];
    },
    wordCount() {
      return this.currentValue.length + (this.maxlength ? `/${this.maxlength}` : "");
    },
  },
  mounted() {
    if (this.type !== "textarea") {
      this.prepend = this.$slots.prepend !== undefined;
      this.append = this.$slots.append !== undefined;
      this.showPrefix = this.prefix !== "" || this.$slots.prefix !== undefined;
      this.showSuffix = this.suffix !== "" || this.$slots.suffix !== undefined;
    } else {
      this.prepend = false;
      this.append = false;
    }
    this.slotReady = true;
    this.resizeTextarea();
  },
};
</script>
<style lang="less">
.icon-error {
  cursor: pointer;
}
.tb-input-wrapper {
  display: inline-block;
  width: 100%;
  position: relative;
  vertical-align: middle;
  line-height: 1.5715;
  label {
    display: inline-block;
    width: 100%;
    height: auto;
  }
  &:hover {
    .tb-input-icon-clear {
      display: block;
    }
  }
  .tb-input-icon-clear .icon-error {
    vertical-align: baseline;
    cursor: pointer;
  }
  &.tb-input-group-with-prepend,
  &.tb-input-group-with-append {
    display: inline-flex;
    .tb-input {
      &:focus,
      &:hover {
        z-index: 1;
      }
    }
  }
  .tb-input-group-prepend,
  .tb-input-group-append {
    position: relative;
    button {
      border-radius: 0;
    }
  }
  .tb-input-group-prepend {
    z-index: 1;
    right: -1px;
  }

  .tb-input-group-append {
    z-index: 0;
    left: -1px;
  }
}
// 
