  .tb-input-with-prefix {
    padding-left: 40px;
  }
  .tb-input-prefix,
  .tb-input-suffix {
    width: 40px;
    i {
      font-size: 18px;
      line-height: 40px;
      height: 40px;
    }
  }
  .tb-input-icon {
    line-height: 40px;
    height: 40px;
  }
}
.tb-input-wrapper-small {
  .tb-input-with-prefix {
    padding-left: 35px;
  }
  .tb-input-prefix,
  .tb-input-suffix {
    width: 35px;
    i {
      line-height: 35px;
      height: 35px;
    }
  }
  .tb-input-icon {
    line-height: 35px;
    height: 35px;
  }
}
.tb-input-wrapper-mini {
  .tb-input-with-prefix {
    padding-left: 30px;
  }
  .tb-input-prefix,
  .tb-input-suffix {
    width: 30px;
    i {
      line-height: 30px;
      height: 30px;
    }
  }
  .tb-input-icon {
    line-height: 30px;
    height: 30px;
  }
}

