//  name
import Vue from "vue";
import LoadingBar from "./main.vue";

/**
 * @description:    Vue.extend(LoadingBar)   Vue new
 * @param {*}
 * @return {*}
 */
let LoadingBarConstructor = Vue.extend(LoadingBar);

let timer = null;
let removeTimer = null;

/**
 * @description:     
 * @param {*}
 * @return {*}
 */

let tbLoadingBar = () => {
  return new LoadingBarConstructor();
};

/**
 * @description:
 * @param {*} options  prototypethis this this data
 * @return {*}
 */
LoadingBarConstructor.prototype.config = function(options) {
  // 
  Object.keys(options).forEach((key) => {
    // isError    totalProgress  
    if (key === "isError" || key === "totalProgress") {
      // 
      throw new Error("!");
    }
    // data  key
    this[key] = options[key];
  });
  // this.start()
  this.start();
};

/**
 * @description: vue document.body 
 * @param {*}
 * @return {*}
 */
LoadingBarConstructor.prototype.init = function() {
  //  
  clearTimeout(timer);
  // 0
  this.totalProgress = 0;

  // false
  this.isError = false;

  // new  vue  this.vm
  this.vm = this.$mount();
  // vue
  // console.log(this === this.vm);
  // this.vm.$el  creatElement document
  /**
   * Vue2   
   *   this.$mount new MyComponent()
   *  var MyComponent = Vue.extend({
        template: '<div>Hello!</div>'
      })

      //  #app ( #app)
      new MyComponent().$mount('#app')

      // 
      new MyComponent({ el: '#app' })

      // 
      var component = new MyComponent().$mount()
      document.getElementById('app').appendChild(component.$el)
   */
  // 
  document.body.appendChild(this.vm.$el);
  // debugger
  return this;
};

/**
 * @description: api
 * @param {*}
 * @return {*}
 */
LoadingBarConstructor.prototype.start = function() {
  // api 
  this.init();
  /**
   * @description:   
   * @param {*} setInterval
   * @return {*}
   */
  timer = setInterval(() => {
    //   90    ajaxend
    if (this.totalProgress < 90) {
      //   * 
      this.totalProgress += (this.percentNum || Math.random()) * this.speed;
    }
  }, 100);
};
/**
 * @description: api
 * @param {*}
 * @return {*}
 */
LoadingBarConstructor.prototype.end = function() {
  //   timer   init
  timer || this.init();
  // 
  setTimeout(() => {
    // 100
    this.totalProgress = 100;
    //   
    clearTimeout(removeTimer);
    // 200
    removeTimer = setTimeout(() => {
      // timer
      clearTimeout(timer);
      // timer
      timer = null;
      // 
      document.body.removeChild(this.vm.$el);
    }, 200);
  }, 0);
  // clearTimeout(newTimer);
};

LoadingBarConstructor.prototype.error = function() {
  /**
   * @description: end
   * @param {*}
   * @return {*}
   */

  this.end();
  //   100 200msisError  
  // this.totalProgress = 100;
  this.isError = true;
};

// 
export default tbLoadingBar();
