<template>
  <div class="tb-input-number">
    <div @click="decreaseNumber" @mousedown="whileMouseDown(decreaseNumber)" @mouseup="clearTimer" :class="buttonClass">-</div>
    <input type="number" v-bind:value="numericValue" @keypress="validateInput" @input="inputValue" :class="inputClass" :min="min" :max="max" debounce="500" />
    <div @click="increaseNumber" @mousedown="whileMouseDown(increaseNumber)" @mouseup="clearTimer" :class="buttonClass">+</div>
  </div>
</template>

<script>
require("../utils/utils");
import Emitter from "../mixins/emitter";
export default {
  name: "tbInputNumber",
  mixins: [Emitter],
  data: function() {
    return {
      numericValue: this.value,
      timer: null,
    };
  },
  props: {
    //   v-model
    value: {
      type: Number,
      default: 0,
    },
    // 
    min: {
      default: 0,
      type: Number,
    },
    // 
    max: {
      default: 100,
      type: Number,
    },
    //   15  
    step: {
      default: 1,
      type: Number,
    },
    // 
    mouseDownSpeed: {
      default: 100,
      type: Number,
    },
    // 
    inputClass: {
      default: "tb-input-number__input",
      type: String,
    },
    // class
    buttonClass: {
      default: "tb-input-number__button",
      type: String,
    },
    /**
     * @description:   true 
     * @param {*}
     * @return {*}
     */

    integerOnly: {
      default: false,
      type: Boolean,
    },
  },
  methods: {
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */

    clearTimer() {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
    },
    /**
     * @description: 
     * @param {*} callback
     * @return {*}
     */

    whileMouseDown(callback) {
      if (this.timer === null) {
        this.timer = setInterval(() => {
          callback();
        }, this.mouseDownSpeed);
      }
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */

    increaseNumber() {
      this.numericValue = Number.prototype.add(this.numericValue, this.step);
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */

    decreaseNumber() {
      this.numericValue = Number.prototype.sub(this.numericValue, this.step);
    },
    isInteger(evt) {
      evt = evt ? evt : window.event;
      let key = evt.keyCode || evt.which;
      key = String.fromCharCode(key);
      const regex = /[0-9]/;
      //     0-9
      if (!regex.test(key)) {
        evt.returnValue = false;
        // 
        if (evt.preventDefault) evt.preventDefault();
      }
    },
    isNumber(evt) {
      // 
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      // 
      if (charCode > 31 && (charCode < 48 || charCode > 57) && charCode !== 46) {
        // 
        evt.preventDefault();
      } else {
        return true;
      }
    },
    /**
     * @description: 
     * @param {*} evt
     * @return {*}
     */

    validateInput(evt) {
      if (this.integerOnly === true) {
        this.isInteger(evt);
      } else {
        this.isNumber(evt);
      }
    },
    inputValue(evt) {
      this.numericValue = evt.target.value ? Number.prototype.add(evt.target.value, 0) : this.min;
    },
  },
  watch: {
    value: function(val, oldVal) {
      this.numericValue = val;
    },
    numericValue: function(val, oldVal) {
      // ,
      if (val == "") return;
      if (val <= this.min) {
        this.numericValue = Number.prototype.add(this.min, 0);
      }
      if (val >= this.max) {
        this.numericValue = Number.prototype.add(this.max, 0);
      }
      if (val <= this.max && val >= this.min) {
        this.$emit("input", val, oldVal);
        this.dispatch("tbFormItem", "form-change", val);
      }
    },
  },
};
</script>

<style lang="less">
.tb-input-number {
  &__input {
    -webkit-appearance: none;
    border: 1px solid #d9d9d9;
    float: left;
    font-size: 16px;
    height: 40px;
    margin: 0;
    outline: none;
    text-align: center;
    width: calc(100% - 80px);
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
  }
  &__button {
    text-align: center;
    -webkit-appearance: none;
    transition: background 0.5s ease;
    background: #dcdfe6;
    border: 0;
    color: #606266;
    cursor: pointer;
    float: left;
    font-size: 20px;
    height: 40px;
    line-height: 40px;
    margin: 0;
    width: 40px;
  }
}
</style>
